import React, {useState} from "react";
import styles from "./Nav.module.scss";

const Nav = () => {

    let [mobile, setMobile] = useState(
        {isActive: false}
    );

    let mobileMenuActive = mobile.isActive ? styles.linkMobile : styles.link

    function activeMobile() {
        setMobile({isActive: !mobile.isActive})

    }

    return (
        <div>
            <nav className={styles.nav}>
                <a onClick={activeMobile} className={styles.btnMenu}>Menu</a>
                <a href=".#" className={mobileMenuActive}>Скиллы</a>
                <a href=".#" className={mobileMenuActive}>Проекты</a>
                <a href=".#" className={mobileMenuActive}>Контакты</a>
            </nav>
        </div>
    )
};

export default Nav;